.gallery-div {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: auto;
    height: 500px;
    margin: 2rem;
    border-radius: 10px;
    &__carroussel {
        height: 100%;
        width: 100%;
        display: flex;
        &__photo {
            width: 100%;
            height: 100%;
            object-fit: cover;
            flex-shrink: 0;
            flex-grow: 0;
            transition: transform 300ms ease-in-out;
        }
    }
    &__for-arrow {
        position: absolute;
        top: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        &.for-arrow-left {
            left: 20px;
        }
        &.for-arrow-right {
            right: 20px;
        }
        .gallery-div__arrow {
            cursor: pointer;
            transition: transform 200ms ease-in-out;
            &.arrow-left {
                transform: rotate(180deg);
                &:hover {
                    transform: scale(1.3) rotate(180deg);
                }
            }
            &.arrow-right:hover {
                transform: scale(1.3);
            }
        }
    }
    &__numbering {
        position: absolute;
        bottom: 30px;
        left: 0px;
        right: 0px;
        text-align: center;
        color: white;
    }
}

@media (max-width: 738px) {
    .gallery-div {
        margin-top: 27px;
        margin-left: 0px;
        margin-right: 0px;
        margin-bottom: 20px;
        &__for-arrow {
            .gallery-div__arrow {
                height: 20px;
            }
        }
        &__numbering {
            display: none;
        }
    }
}
